const preloaderDisplayTime = 100;

window.addEventListener('load', function () {
    const preloader = document.getElementById('preloader-1');
    setTimeout(function () {
        preloader.style.display = 'none';
    }, preloaderDisplayTime);

    document.querySelectorAll("td").forEach(td => {
        if (td.textContent.trim() === "OUI") {
            td.style.color = "#009900";
        } else if (td.textContent.trim() === "NON") {
            td.style.color = "#990000";
        }
        else if (td.textContent.trim() === "OUI") { td.style.color = "#387633"; }
        else if (td.textContent.trim() === "PERMISE DE FACTO") { td.style.color = "#84AC03"; }
        else if (td.textContent.trim() === "AVEC DES RÉSERVES") { td.style.color = "#B3C500"; }
        else if (td.textContent.trim() === "NON LÉGIFÉRÉE") { td.style.color = "#F1C232"; }
        else if (td.textContent.trim() === "LÉGISLATIONS CONTRADICTOIRES") { td.style.color = "#BF9000"; }
        else if (td.textContent.trim() === "BIENTÔT INTERDITE") { td.style.color = "#E69138"; }
        else if (td.textContent.trim() === "INTERDITE DE FACTO") { td.style.color = "#EA4353"; }
        else if (td.textContent.trim() === "NON") { td.style.color = "#980000"; }
        else if (td.textContent.trim() === "CONSANGUINE UNIQUEMENT") { td.style.color = "#674EAB"; }
        else if (td.textContent.trim() === "DIFFÈRE PAR ÉTAT") { td.style.color = "#1155CC"; }
        else if (td.textContent.trim() === "INCERTAIN") { td.style.color = "#666666"; }

    });
});

document.addEventListener("DOMContentLoaded", function () {
    languages();
})

function languages() {
    const spanish = document.getElementById('spanish');
    const english = document.getElementById('english');
    const french = document.getElementById('french');
    spanish.addEventListener('click', () => {
        console.log("Español");
    });
    english.addEventListener('click', () => {
        console.log("English");
    });
    french.addEventListener('click', () => {
        console.log("Francais");
    });
}

document.getElementById("return").addEventListener("click", function () {
    window.location.href = "/";
});
